import React, {useState} from 'react';
import Title from "../../../components/title";
import GridView from "../../../containers/grid-view";
import {KEYS} from "../../../constants/keys";
import {URLS} from "../../../constants/urls";
import Form from "../../../containers/form";
import {get} from "lodash";
import InputSearch from "../../../components/input";
import {Input} from "../../../containers/form/components";
import {useTranslation} from "react-i18next";
import {useGetAllQuery} from "../../../hooks/api";
import {OverlayLoader} from "../../../components/loader";
import {useStore} from "../../../store";


const TranslationsContainer = () => {
    const [search, setSearch] = useState('')
    const {data, isLoading} = useGetAllQuery({key: KEYS.locales, url: URLS.locales})
    const {t} = useTranslation()
    const [open, setOpen] = useState(false)
    let columns = get(data, 'data.payload.data', []).map(_locale => ({title: get(_locale, 'display'), key: get(_locale, 'code')}))
    const user = useStore(state => get(state, "user", null))
    columns = [{
        title: 'Key',
        key: 'key',
    }, ...columns]
    if (isLoading) {
        return <OverlayLoader/>
    }
    return (
        <div>
            <div className="grid grid-cols-12 items-center">
                <div className="col-span-8">
                    <Title>{t("Translations")}</Title>
                </div>
                <div className="col-span-12 mt-8">
                    <GridView
                        filters={<div className={'grid grid-cols-12 mb-5 gap-x-8'}>
                            <div className="col-span-3">
                                <InputSearch setValue={setSearch} value={search} placeholder={t('Поиск')}/>
                            </div>
                        </div>}
                        ModalBody={(onSubmit, defaultValues = {}) => <Form onSubmit={(data) => onSubmit(data)}
                                                                           defaultValues={{
                                                                               ...get(defaultValues, 'data', {}),
                                                                           }}>

                            <Input property={{disabled: true}} name={'key'}
                                   placeholder={t('Key')}
                                   label={t('Key')}
                            />
                            {get(data, 'data.payload.data', []).map(_locale => <Input classNames={'mt-2'} name={get(_locale, 'code')}
                                                                         placeholder={get(_locale, 'display')}
                                                                         label={get(_locale, 'display')}
                            />)

                            }
                            <button type={'submit'}
                                    className={'w-full p-3.5 rounded-lg bg-primary block  text-white font-bold text-center h-12 mt-6'}>
                                {t('Save')}
                            </button>
                        </Form>}
                        openCreateModal={open}
                        setOpenCreateModal={setOpen}
                        hasActionColumn
                        listKey={KEYS.translations}
                        url={URLS.translations}
                        columns={columns}
                        dataKey={'data.payload.data'}
                        metaDataKey={'data.payload.meta'}
                        rowKey={'key'}
                        params={{search}}
                        viewUrl={URLS.translationsKey}
                    />
                </div>
            </div>
        </div>
    );
};

export default TranslationsContainer;